<template>
  <div class="menu">
    <van-swipe class="my-swipe" :autoplay="3500" indicator-color="white" style="height:163.03px">
      <van-swipe-item v-for="(item,index) in bannerData" :key="index">
        <img class="auto-img bannerImg" alt="
        图片未加载..." v-lazy="item.bannerImg" />
      </van-swipe-item>
    </van-swipe>
    <div class="content">
      <div class="list fl">
        <van-sidebar v-model="active" @change="toggleNav" >
          <van-sidebar-item v-for="(item,index) in slideData" :key="index" :title="item.typeDesc" />
        </van-sidebar>
      </div>
      <div class="commodity fl">
        <ul class="clearFix">
          <li class="fl" v-for="(item,index) in productData" :key="index" @click="goDetail(item.pid)">
            <div class="imgBox fl">
              <img class="auto-img smallImg" :src="item.smallImg" alt />
            </div>
            <div class="title fl">
              <span class="name">{{item.name}}</span>
              <span class="enname">{{item.enname}}</span>
              <span class="price">￥{{item.price}}</span>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/css/menu.less";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("menuModule");

export default {
  data() {
    return {
      active: 0,
      bannerData: [],
      slideData: [],
      productData: []
    };
  },
  created() {
    this.getBannerData();
    this.getSlidData();
    this.getProductType({type:"isHot"});
  },
  computed: {
    ...mapState(["bannerDataCache", "slideDataCache", "productDataCache"])
  },
  methods: {
    ...mapMutations(["changeData", "changeProducts"]),

    // 获取banner数据
    getBannerData() {
      if (this.bannerDataCache.length > 0) {
        this.bannerData = this.bannerDataCache;
        
        return;
      }
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "get",
        url: "/banner",
        params: {
          appkey: this.appkey
        }
      })
        .then(result => {
          // 关闭加载提示框
          this.$toast.clear();
          // 
          if (result.data.code == 300) {
            
            this.bannerData = result.data.result;
            // 
            this.changeData({
              key: "bannerDataCache",
              value: result.data.result
            });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 获取侧边栏数据
    getSlidData() {
      if (this.slideDataCache.length > 0) {
        this.slideData = this.slideDataCache;
        
        return;
      }
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "get",
        url: "/type",
        params: {
          appkey: this.appkey
        }
      })
        .then(result => {
          // 关闭加载提示框
          this.$toast.clear();
          // 
          if (result.data.code == 400) {
            result.data.result.forEach(value => {
              value.isActive = false;
            });
            result.data.result.unshift({
              type: "isHot",
              typeDesc: "最新推荐",
              isActive: true
            });
            this.slideData = result.data.result;
            // 缓存侧栏数据在vuex
            this.changeData({
              key: "slideDataCache",
              value: result.data.result
            });
            
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 切换标签
    toggleNav(index) {
      this.getProductType({type:this.slideData[index].type});
      
      
    },
    // 获取商品数据
    getProductType(item) {
      // 
    
      if (this.productDataCache[item.type]) {
        this.productData = this.productDataCache[item.type];
        
        return;
      }
      this.productData = [];

      let params = {
        appkey: this.appkey,
        key: "type",
        value: item.type
      };

      // 热门推荐
      if (item.type == "isHot") {
        params.key = "isHot";
        params.value = 1;
      }

      // 加载提示框
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0 //不自动关闭
      });

      this.axios({
        method: "get",
        url: "/typeProducts",
        params
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 500) {
            this.productData = result.data.result;
            // 缓存商品数据
            this.changeProducts({
              type: item.type,
              value: result.data.result
            });
            
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 跳转到商品详情
    goDetail(pid){
      // pid商品id
      this.$router.push({name:"Detail",query:{pid}});
    }
  }
};
</script>

<style lang="less" scoped>
</style>